
import { defineComponent } from 'vue';
import fComponent from '@/components/util/fComponent.vue';

import { IPData, IPDataContent } from '../components/models';

export default defineComponent({
  name: 'productsView',
  components: {  
    fComponent,
  },
  data () {
    return { 
      products: ["lr", "lrr", "tfr", "lpr", "nyr", "nyr5", "pr4", "pr3", "dr"],
      sIndex: 0,
      slide: 0,
      switched: false,

      pData: [] as IPData[],
       
    };
  },
  methods:{
    selectProduct(index: number){
      this.sIndex = index;
    },

    async getProducts(){
      const res = await this.$api.get("/product");
      if(res.success){
        this.formatProducts(res.data);
      }

    },
    formatProducts(data: []){

      //Loop over products and pull the basic info
      // eslint-disable-next-line  @typescript-eslint/no-explicit-any
      data.forEach((p: any) => {

        //Loop over data and create arrays out of str
        let prodData: IPDataContent[] = [];
        // eslint-disable-next-line  @typescript-eslint/no-explicit-any
        p.data.forEach( (d: any) => {
          prodData.push(
            {
              header: d.header,
              strong: d.strong,
              description: d.description ? d.description.split("|!|") : [],
              lis: d.lis ? d.lis.split("|!|") : [],
            }
          )
        });

        //Get table
        let table: string [][] = [];
        if(p.table){
          //table?: string[][]
          // eslint-disable-next-line  @typescript-eslint/no-explicit-any
          p.table.forEach((t: {t: string}) => {
            table.push(
              t ? t.t.split("|!|") : [],
            )
          });
        }

        this.pData.push(
          {
            prodId: p.prodId,
            pName: p.pName,
            data: prodData,
            table
          }
        );

      });
      this.switched = true;

    }


  },
  watch:{
    slide(){
      this.sIndex = this.slide;
    },
    sIndex(){
      this.switched = false;
      setTimeout(() => {
        this.switched = true;
      }, 500);
    }
  },
  mounted(){
    this.getProducts();
  }


});
